import { Component, ViewChild } from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  Content,
  Events
} from 'ionic-angular';
import { Chat } from '../../providers/backend-api/chat';
import { BackendApiProvider } from '../../providers/backend-api/backend-api';
import * as $ from 'jquery';

/**
 * Generated class for the ChatPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'chat.html'
})
export class ChatPage {
  chat: Chat;
  chatName: string;
  msg: string;
  isNewChat: boolean = false;
  @ViewChild('chatScroll') private content: Content;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private api: BackendApiProvider,
    public events: Events
  ) {
    if (navParams.data.chat != null) {
      this.chat = navParams.data.chat;
    } else {
      this.isNewChat = true;
    }
    this.chatName = navParams.data.chatName;
    if (!this.isNewChat) {
      setInterval(() => {
        this.api.getChat(this.chat.uuid).subscribe(data => {
          this.chat = data.chat;
          try {
            setTimeout(() => {
              if (this.content._scroll) this.content.scrollToBottom(300);
            }, 10);
          } catch (err) {}
        });
      }, 500);
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChatPage');
    if (!this.isNewChat) {
      try {
        setTimeout(() => {
          if (this.content._scroll) this.content.scrollToBottom(300);
        }, 100);
      } catch (err) {}
    }
  }

  sendMsg() {
    if (this.msg != '') {
      if (this.chat == null) {
        let otherUser = this.navParams.data.chatName;
        let uuid = this.navParams.data.uuid;
        this.api.newChat(otherUser, uuid, this.msg).subscribe(data => {
          if (data.success) {
            this.chat = data.chat;
            this.isNewChat = false;
            this.msg = '';
            this.events.publish('Chat:added', this.chat);
            setInterval(() => {
              this.api.getChat(this.chat.uuid).subscribe(data => {
                this.chat = data.chat;

                try {
                  setTimeout(() => {
                    if (this.content._scroll) this.content.scrollToBottom(300);
                  }, 10);
                } catch (err) {}
              });
            }, 500);
          }
        });
      } else {
        this.api.sendMsg(this.chat.uuid, this.msg).subscribe(data => {
          if (data.success == true) {
            this.msg = '';
          }
        });
      }
    }
  }
}



