import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { BackendApiProvider } from '../../providers/backend-api/backend-api';

/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html'
})
export class SignupPage {
  name: string;
  email: string;
  username: string;
  password: string;
  password2: string;
  err: string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private api: BackendApiProvider,
    private events: Events
  ) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
  }

  signup() {
    if (
      !this.name ||
      !this.email ||
      !this.username ||
      !this.password ||
      !this.password2
    ) {
      this.err = 'Please Fill All Fields';
      setTimeout(() => {
        this.err = '';
      }, 1500);
      return;
    } else if (this.password != this.password2) {
      this.err = 'Password Do Not Match';
      setTimeout(() => {
        this.err = '';
      }, 1500);
      return;
    } else {
      this.api
        .signup(
          this.name,
          this.email,
          this.username,
          this.password,
          this.password2
        )
        .subscribe(data => {
          if (!data.success) {
            this.err = data.err;
            setTimeout(() => {
              this.err = '';
            }, 1500);
          } else {
            this.api.login(this.username, this.password).subscribe(data => {
              if (data.success === true) {
                this.events.publish('Login:success');
              } else if (data.err) {
                this.err = data.err;
                setTimeout(() => {
                  this.err = '';
                }, 1500);
              }
            });
          }
        });
    }
  }
}



