import { Component } from '@angular/core';
import { NavController, App, Events } from 'ionic-angular';
import { BackendApiProvider } from '../../providers/backend-api/backend-api';
import { Chat } from '../../providers/backend-api/chat';
import { ChatPage } from '../chat/chat';
import { InitialEntryTabsPage } from '../initial-entry-tabs/initial-entry-tabs';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  chats: Chat[];
  user: string;
  unread: number;
  constructor(
    public navCtrl: NavController,
    private api: BackendApiProvider,
    private app: App,
    public events: Events
  ) {
    this.events.subscribe('Chat:added', chat => {
      this.chats.push(chat);
    });
    api.getAllChats().subscribe(data => {
      this.chats = data.chats;
      this.user = data.user;
    });
    setInterval(() => {
      api.getAllChats().subscribe(data => {
        this.chats = data.chats;
        this.user = data.user;
      });
    }, 500);
  }

  openChat(uuid: string) {
    this.api.getChat(uuid).subscribe(data => {
      this.app
        .getRootNav()
        .push(ChatPage, { chat: data.chat, chatName: data.chatName });
    });
  }
  logout() {
    this.api.logout().subscribe(data => {
      if (data.success) {
        this.events.publish('Logout:success');
      }
    });
  }
}



