import { Component } from '@angular/core';
import { NavController, NavParams, Events, Platform } from 'ionic-angular';
import { BackendApiProvider } from '../../providers/backend-api/backend-api';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {
  username: string;
  password: string;
  err: string;
  msg: string;
  isConnected: boolean = true;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private api: BackendApiProvider,
    public events: Events
  ) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  login() {
    if (!this.username || !this.password) {
      this.err = 'Please Fill All Fields';
      setTimeout(() => {
        this.err = '';
      }, 1500);
      return;
    }

    this.api.login(this.username, this.password).subscribe(data => {
      if (data.success === true) {
        this.events.publish('Login:success');
      } else if (data.err) {
        this.err = data.err;
        setTimeout(() => {
          this.err = '';
        }, 1500);
      }
    });
  }
}



